import React from 'react'
import Intro from '../components/Intro'
import Card from '../components/Card'

const intro ={
	content: `Program pelatihan belajar pemrograman dengan tatap muka secara offline dan dengan bimbingan para instruktur professional. Peserta akan belajar kurikulum yang berorientasi pada industri dengan teknologi terbaru..`
}

const cards =[
	{
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg',
		title:'Weekend bootcamp',
		subtitle:'Kelas belajar coding yang diselenggarakan di setiap akhir pekan, berlokasi di Bandung dan Jakarta'
	},
	{
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg',
		title:'Intensive bootcamp',
		subtitle:'Kelas Web dan Android secara intensive selama satu minggu dengan sistem karantina'
	},
]

const DevSchool = () => (
	<div className="container">
		<h1 style={{textAlign: 'center' }}>DEVELOPER DEVSCHOOL</h1>
		<div className="row" style={{marginTop: 40, marginButtom: 100 }}>
			<Intro content={intro.content}/>
			<div className="col-md-6">
				<img
					src="https://devschool.id/resources/themes/default/assets/img/foto-devschool.jpg"
					alt=""

					
				/>
			</div>
		</div>

		<div className="row" style={{ marginButtom: 100 }}>
		{
			cards.map(card =>
				<Card
					imageUrl={card.imageUrl}
					title={card.title}
					subtitle={card.subtitle}
					/>
				)
		}
		</div>
	</div>

)

export default DevSchool