import React from 'react'

const Pmis = (props) => (

	props.pmis.map((pmi) =>(

			<div className="col-md-6" key={pmi.id}>
              <div className="card" style={{margin: 10}}>
                <div className="card-body">
            		<h5 className="card-title">{pmi.id}</h5>
            		<h5 className="card-title">{pmi.unit}</h5>
            		<h5 className="card-title">{pmi.provinsi}</h5>
            		<h5 className="card-title">{pmi.jumlah}</h5>
              	</div>
           	 </div>
           </div>
		))
    )

export default Pmis;