import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro ={
	content: `Semua yang kamu butuhkan untuk memulai belajar coding dan meningkatkan keahlian codingmu hingga mahir 
	dengan alur belajar yang super jelas dan terarah. 
	Pilih jalur belajarmu, mulai belajar coding sekarang dan 
	jadilah developer profesional!`
}

const features = [
	{ title: 'Materi Belajar', desc: 'Materi belajar disusun secara sistematis dengan metode dan teknologi terbaru.'

	},

	{ title: 'Waktu Belajar', desc: 'Waktu belajar sangat fleksibel'

	},

	{ title: 'Metode Belajar', desc: 'Kamu hanya perlu mengikuti alur belajar yang telah disediakan'

	},
]

const cards = [
	{
		title: 'Menjadi Web Programmer', imageUrl: 'https://static.cdn-cdpl.com/source/2/Mengenal_Pemrograman_Komputer.jpg'
	},
	{
		title: 'Belajar Pemrograman PHP', imageUrl: 'https://static.cdn-cdpl.com/source/2/Belajar_Pemrograman_PHP.jpg'
	},


]

const Premium = () => (

	<div className="container">
		<h1 style={{ textAlign: 'center'}}>PREMIUM ONLINE COURSE</h1>
			<div className="row" style={{marginTop: 40, marginButtom: 100}}>
				<Intro content={intro.content}/>

				<div className="col-md-6" style={styles.box450border}>
				{
					features.map(feature => (
						<div>
							<h5><strong>{feature.title}</strong></h5>
							<p>{feature.desc}</p>
						</div>
						))
				}
				</div>

				<div className="row" style={{ marginButtom: 50}}>
				{
					cards.map(card =>
						<Card
							imageUrl={card.imageUrl}
							title={card.title}
							/>
					)
				}
				</div>
			</div>
			
	</div>
)

const styles = {
	box450border: {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius: 10,
		padding: 30
	},
}

export default Premium

