import React, {Component} from 'react';
import axios from 'axios';
import Pmis from '../Unit/Pmis'


class Blog extends Component{

  constructor(props){

    super(props)
    this.state = {
      pmis: [],
      
    }
}
    componentDidMount(){
    axios.get('https://script.googleusercontent.com/macros/echo?user_content_key=chst3jbKyUrpKZhsIgjK3qGEazpWKXg4FBB9euU4vqaA5uvQA6kxF9_W7lD3qLhfuB6SyDHusyfqKkJt1UILCVBRHPbsHTheOJmA1Yb3SEsKFZqtv3DaNYcMrmhZHmUMWojr9NvTBuB6lHT6qnqYcmFWggwoSVQQXTsQ1HqKa1CgDXQROm1OeNR5ibYVAaRxAeOtzLmbRZcVjrce7Uveb8iU1s-L39A_vQK8SSXHAAREEMoiQ31HA2S2RLK1Djh8MGUS4MhBQRZ5dO17jneK-A4MAdorMS0Z&lib=M-tpZm-Fm1QX9Yr80nZn_p-WXe3zpGnIr')
    .then(response =>{
      this.setState({ pmis: response.data.data})
        console.log('response',response)
    })
    .catch(error =>{
        console.log('error',error)
    })
  
  }



  render(){

    return(
      <div className ="container">
        <h1>Stok Darah Palang Merah Indonesia</h1>
        <div className="row">
          <div className="col-md-6" style={{border: '1px solid black'}}>
          <h2>Unit</h2>
            <div className="row">
              <Pmis
              pmis={this.state.pmis}/>
            </div>
          </div>
        </div>
       </div>
       );
  	}
  }
  
export default Blog;

