import React, {Component} from 'react';
import axios from 'axios';
class form extends Component{

  constructor(props){

    super(props)
    this.state = {
      members: [],
      name: '',
      email: '',
      name_new:'',
      email_new:'',
      buttonDisabled : false,
      formStatus: '',
      memberIdSelected: null,
    }
  }

  componentDidMount(){
    axios.get('https://reqres.in/api/users?page=1')
    .then(response =>{
      this.setState({ members: response.data.data })
        console.log('response',response)
    })
    .catch(error =>{
        console.log('error',error)
    })
  }

  inputOnChangeHandler = (event) => {
    this.setState({[event.target.name]: event.target.value})
  }

  onSubmitHandler = (e) => {
    console.log('form di submit')
    e.preventDefault()
    this.setState({ buttonDisabled: true })
    var payload = {
      name: this.state.name,
      email: this.state.email
    }
    //url untuk form create
    var url = 'https://reqres.in/api/users'

     axios.post(url, payload)
      .then(response => {
        var members = [...this.state.members]
        members.push(response.data)
         this.setState ({
      members: [],
      name: '',
      email: '',
      name_new: response.data.name,
      email_new: response.data.email,
      formStatus: 'Registered!',
      buttonDisabled : false,
      memberIdSelected: null
    })
           console.log('response', response.data)
      })

    .catch(error => {
      console.log(error)

        })
    }
    
 

   

render (){
  return(
  <div className ="container">
      <div className="row" >
      
          <h2>Form Register</h2>
            <div className="row" style={{marginTop: 40}}>
            <div className="col-md-6" style={styles.box450border}>


	           <form onSubmit={this.onSubmitHandler}>
                <div className="form-group">
                  <label>Name</label>
                  <input type="text" className="form-control"
                  name="name" value={this.state.name} onChange={this.inputOnChangeHandler}/>
                  </div>

                <div className="form-group">
                  <label>Email</label>
                  <input type="email" className="form-control"
                  name="email" value={this.state.email} onChange={this.inputOnChangeHandler}/>
                </div>

                  <button type="submit" className="btn btn-primary" disabled={this.buttonDisabled}>Submit</button>
              </form>
              </div>
              
          
        
        <div className="col-md-6"  style={styles.box450border}>
          <form>
                <div className="form-group">
                  <label>Name</label>
                  <input type="text" className="form-control"
                  name="name_new" value={this.state.name_new} onChange={this.inputOnChangeHandler} disabled/>
                </div>

                <div className="form-group">
                  <label>Email</label>
                  <input type="email" className="form-control"
                  name="email_new" value={this.state.email_new} onChange={this.inputOnChange} disabled/>
                </div>

                  <div className="form-group">
                  <h4>{this.state.formStatus}</h4>
                  </div>
          </form>

        </div>
        </div>
      </div>
      </div>
	);
}
}
const styles = {
  box450border: {
    height: '450px',
    border: '2px solid #f1f1f1',
    borderRadius: 10,
    padding: 30
  },
}

export default form